<template lang="html">
  <div class="article-list">
    <article class="block post wysiwyg" v-for="item in list">
      <h2>{{ item.title }}</h2>
      <p class="article-meta">发布于 {{ item.uploadDate }}</p>
      <div class="ui ribbon label red">
        <a href="">{{ item.kind }}</a>
      </div>
      <div class="abstract" v-html="item.summary"></div>
      <p class="more">
        <router-link
          :to="{ path: '/article', query: { articleId: item.articleId } }"
          >阅读全文</router-link
        >
      </p>
    </article>
    <div class="pages">
      <a href="javascript:;" @click="go((page -= 1))" style="float: left;"
        >上一页</a
      >
      <a href="javascript:;" @click="go((page += 1))" style="float: right;"
        >下一页</a
      >
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  props: ["tagSelect"],
  data() {
    return {
      list: [],
      page: 1,
      pageSize: 10,
      count: 0,
    };
  },
  watch: {
    tagSelect() {
      this.getTagList();
    },
  },
  mounted() {
    this.getlist();
  },
  methods: {
    getlist() {
      var param = {
        page: this.page,
        pageSize: this.pageSize,
      };
      axios
        .post("/api/article/articleList", {
          params: param,
        })
        .then((result) => {
          let res = result.data;
          if (res.status == "success") {
            if (res.data.total == 0) {
              this.page -= 1;
              return;
            } else {
              this.list = res.data.records;
            }
          } else {
            this.list = [];
          }
        });
    },
    getTagList() {
      var param = {
        page: this.page,
        pageSize: this.pageSize,
        kindname: this.tagSelect,
      };
      axios
        .post("/api/article/getByTags", {
          params: param,
        })
        .then((result) => {
          let res = result.data;
          if (res.status == "success") {
            if (res.data.total == 0) {
              this.page -= 1;
              return;
            } else {
              this.list = res.data.records;
            }
          } else {
            this.list = [];
          }
        });
    },
    go() {
      if (this.page < 1) {
        this.page = 1;
        return;
      } else {
        this.getlist();
      }
    },
  },
};
</script>

<style media="screen">
.article-list {
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  /*box-shadow: 1px 1px 2px rgba(0,0,0,0.08)*/
  box-shadow: 0 16px 24px 1px rgba(0, 0, 0, 0.14),
    0 6px 50px 1px rgba(0, 0, 0, 0.12), 0 6px 10px -5px rgba(0, 0, 0, 0.2);
}
</style>



